import { Component, OnInit, Input, Output, EventEmitter, OnChanges, SimpleChanges, AfterContentChecked } from '@angular/core';

@Component({
  selector: 'app-sub-product-templates',
  templateUrl: './sub-product-templates.component.html',
  styleUrls: ['./sub-product-templates.component.css']
})
export class SubProductTemplatesComponent implements OnInit {

  @Input() SubProductTemplate: any;
  public subProducts;
  shoesProduct = [
    '#sportshoes',
    '#redshoes',
  ];

  sandalProduct = [
    '#flatshoes',
    '#heels',
  ];

  constructor() { }
  ngOnInit() {
     this.subProducts = this.shoesProduct.concat(this.sandalProduct);
  }

}
