import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IProducts } from './products';
import { MProducts } from './main-products-list';
import { Observable, of } from 'rxjs';
import { map } from 'rxjs/internal/operators/map';



@Injectable({
  providedIn: 'root'
})
export class ItemlistService {

  // tslint:disable-next-line:no-inferrable-types
  // tslint:disable-next-line:variable-name
  // private _url = 'https://raw.githubusercontent.com/ummeq/ummeq.github.io/master/assets/data/products.json';
   // tslint:disable-next-line:variable-name
   private _url = 'https://raw.githubusercontent.com/ummeq/ummeq.github.io/master/assets/data/sub_products_list';
   private Mainurl = 'https://raw.githubusercontent.com/ummeq/ummeq.github.io/master/assets/data/main_products_list';
  
   private PARAMS = new HttpParams({
    fromObject: {
      action: 'mainproducts',
      format: 'json',
      origin: '*'
    }
  });

  constructor(private http: HttpClient) { }


  getItemList(): Observable<IProducts[]> {
    return this.http.get<IProducts[]>(this._url);
    }

  // getMainItemList(item: string): Observable<MProducts[]> {
  //   return this.http.get(this.main_url,)

  // }

  // search(term: string) {
  //   if (term === '') {
  //     return of([]);
  //   }

  //   return this.http
  //     .get(this.Mainurl, {params: this.PARAMS.set('search', term)}).pipe(
  //       map(response => response[1])
  //     );
  // }


  search(term: string) {
    if (term === '') {
      return of([]);
    }

    return this.http
      .get(this.Mainurl, {params: this.PARAMS.set('search', term)}).pipe(
        map(response => response[1])
      );
  }
}
