import { Component, Directive, ElementRef, ViewChild, EventEmitter, Output, Input, OnInit } from '@angular/core';
import { ItemlistService } from '../itemlist.service';
import { NgbTypeahead } from '@ng-bootstrap/ng-bootstrap';
import { Observable, Subject, merge, of, empty } from 'rxjs';
import { FormControl } from '@angular/forms';
import { map, startWith } from 'rxjs/operators';
import { MatAutocompleteSelectedEvent, MatChipInputEvent } from '@angular/material';
import { COMMA, ENTER } from '@angular/cdk/keycodes';

@Component({
  selector: 'app-search-products',
  templateUrl: './search-products.component.html',
  styleUrls: ['./search-products.component.css']
})


export class SearchProductsComponent implements OnInit{

  public values: any;
  public SelectedValue = [];
  public selectProd;
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = false;
  separatorKeysCodes = [ENTER, COMMA];
  productCtrl = new FormControl();
  filtereProducts: Observable<any[]>;
  Products = [
    '#Shoes',
    '#Sandals'
  ];

  allProducts = [
    '#Shoes',
    '#Sandals',
  ];

  shoesProduct = [
    '#sportshoes',
    '#redshoes',
  ];

  sandalProduct = [
    '#flatshoes',
    '#heels',
  ];

  searchedProducts = [];
  @ViewChild('productInput', { static: false }) productInput: ElementRef;
  @Output() selectedProducts: EventEmitter<any> = new EventEmitter();
  public subProducts;
  constructor(private itemService: ItemlistService) {
  }

  ngOnInit() {
    this.subProducts = this.shoesProduct.concat(this.sandalProduct);
  }

  add(event: MatChipInputEvent): void {
    const input = event.input;
    const value = event.value;

    // Add our products
    if ((value || '').trim()) {
      this.Products.push(value.trim());
    }

    // Reset the input value
    if (input) {
      input.value = '';
    }

    this.productCtrl.setValue(null);
  }

  filter(name: string) {
    return this.allProducts.filter(products =>
      products.toLowerCase().indexOf(name.toLowerCase()) === 0);
  }

  // function after selecting dropdown

  selected(event: MatAutocompleteSelectedEvent): void {
    this.Products.push(event.option.viewValue);
    if(this.Products.length === 0){
      this.subProducts = this.allProducts;
    } else if (this.Products.length === 2) { // if both option selected 
      this.filterDropdown([]);
      this.subProducts = this.shoesProduct.concat(this.sandalProduct);
    } else {
      if (this.Products.indexOf('#Shoes') !== -1) { // if there is shoes in dropdown list
        this.selectProd = ['#Sandals'];
        this.subProducts = this.shoesProduct;
      } else {
        this.selectProd = ['#Shoes'];
        this.subProducts = this.sandalProduct;
      }
    }
    this.searchedProducts = [];
    this.itemService.getItemList().subscribe((itemList: any) => {
      this.Products.forEach((searchString) => {
        itemList.products.filter(i => (i.main_product_id.toUpperCase() === searchString.toUpperCase())).map((i) => {
          this.searchedProducts.push(i);
          });
      });
    });
    this.selectedProducts.emit(this.searchedProducts);
    this.productInput.nativeElement.value = '';
    this.productCtrl.setValue(null);
  }


  // function to remove selected tag
  remove(products: any): void {
    const index = this.Products.indexOf(products);
    if (index >= 0) { // if index value is greter than 1 then remove item
      this.Products.splice(index, 1);
      if (this.Products.length === 0 ) {
        this.searchedProducts = [];
        this.subProducts = [];
        this.filterDropdown(this.allProducts);
        this.selectedProducts.emit(this.searchedProducts);

      } else {
        if (this.Products.indexOf('#Shoes') !== -1) { // if selected item is #shoes
          this.selectProd = ['#Sandals'];
          this.subProducts = this.shoesProduct;
        } else {
          this.selectProd = ['#Shoes'];
          this.subProducts = this.sandalProduct;
        }
        
        this.filterDropdown(this.selectProd);
        this.searchedProducts = []
        this.itemService.getItemList().subscribe((itemList: any) => {
          this.Products.forEach((searchString) => {
            itemList.products.filter(i => (i.main_product_id.toUpperCase() === searchString.toUpperCase())).map((i) => {
              this.searchedProducts.push(i);
            });
          });
        });
        this.selectedProducts.emit(this.searchedProducts);
      }
    }

  }

  // function to filter dropdown
  filterDropdown(productList) {
    this.filtereProducts = this.productCtrl.valueChanges.pipe(
      startWith(null),
      map((products: string | null) =>
        products ? this.filter(products) : productList.slice()));
  }



}
